body {
    margin: 0;
    font-family: Arial, sans-serif;
    background-color: #121212;
    color: white;
  }
  
  .app-container {
    padding: 2rem;
  }
  
  .portfolio-container {
    max-width: 900px;
    margin: auto;
    text-align: center;
  }
  
  .portfolio-title {
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 1rem;
  }
  
  .portfolio-subtitle {
    font-size: 1.5rem;
    color: #bbb;
    margin-bottom: 2rem;
  }
  
  .grid-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 1.5rem;
  }
  
  .card {
    background-color: #1e1e1e;
    padding: 1.5rem;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  }
  
  .card-title {
    font-size: 1.2rem;
    font-weight: bold;
    color: #4A90E2;
    margin-bottom: 0.5rem;
  }
  
  .card-content {
    color: #ccc;
  }
  
  .button-container {
    margin-top: 2rem;
  }
  
  .download-button {
    background-color: #4A90E2;
    padding: 0.75rem 1.5rem;
    border-radius: 5px;
    color: white;
    text-decoration: none;
    font-weight: bold;
  }
  
  .download-button:hover {
    background-color: #357ABD;
  }
  